enum TrimDirection {
    Start = 1 << 0, 
    End =   1 << 1,
}

function _trim(string: string, direction: TrimDirection, ...chars: string[]) {
    const length = string.length;
    let startIndex = 0;
    let endIndex = length; //non-zero-based when taking a substring
    let seekFromStart = !!(direction & TrimDirection.Start);
    let seekFromEnd = !!(direction & TrimDirection.End);

    if (string.length == 0 || chars.length == 0 || (!seekFromStart && !seekFromEnd)) {
        return string;
    }

    for (let i = 0; i < string.length; i++) {
        const startChar = string[i];
        const endChar = string[length - i - 1];

        if (chars.indexOf(startChar) !== -1 && seekFromStart) {
            startIndex++;
        } else {
            seekFromStart = false;
        }

        if (chars.indexOf(endChar) !== -1 && seekFromEnd) {
            endIndex--;
        } else {
            seekFromEnd = false;
        }

        if (!seekFromStart && !seekFromEnd) {
            break;
        }
    }

    return string.substring(startIndex, endIndex);
}

export function trim(string: string, ...chars: string[]) {
    return _trim(string, TrimDirection.Start | TrimDirection.End, ...chars);
}

export function trimStart(string: string, ...chars: string[]) {
    return _trim(string, TrimDirection.Start, ...chars);
}

export function trimEnd(string: string, ...chars: string[]) {
    return _trim(string, TrimDirection.End, ...chars);
}
